@font-face {
    font-family: 'Uncut Sans Bold';
    src: url('/src/assets/fonts/UncutSans-Bold.otf');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Uncut Sans Book';
    src: url('/src/assets/fonts/UncutSans-Book.otf');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'General Sans Bold';
    src: url('/src/assets/fonts/GeneralSans-Bold.otf');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Uncut Sans Semibold';
    src: url('/src/assets/fonts/UncutSans-Semibold.otf');
    font-weight: normal;
    font-style: normal;
}


.works-title{
    font-family: "General Sans Bold";
    font-size: 95.91px;
    font-weight: bold;
    margin: 0;
    /* width: 558px; */
    height: 105px;
    top: 1970px;
    left: 110px;
    color: #CCC5B9;
}

.blog-container {
    max-width: 100%;
    margin:  0 auto;
    padding: 100px;
    padding-top: 4;
    font-family: 'Uncut Sans Book';
    color: #EDE6CF;
    /* background-color: #1E1E1E; */
    /* border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); */
    text-align: left;
}

.blog-container h1 {
    font-family: 'General Sans Bold';
    font-size: 95.91px;
    margin-bottom: 20px;
    color: #BE5F3B;
    margin-top: 0px;
    margin-left: 0;
}

.blog-container h2 {
    font-family: 'Uncut Sans Semibold';
    font-size: 2em;
    margin-top: 20px;
    margin-bottom: 10px;
    color: #EDE6CF;
}

.blog-container h3 {
    font-size: 1.5em;
    margin-top: 20px;
    margin-bottom: 10px;
    color: #EDE6CF;
}

.blog-container p {
    font-size: 1.2em;
    line-height: 1.6;
    margin-bottom: 20px;
    color: #EDE6CF;
}

.photoshopimages{
    width: 100%;
    margin-bottom: 1em;
}

.imagelist{
    columns: 300px 3;
    margin: 20px;
}

